@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  background: transparent;
}

button:focus {
  outline: none;
}

button:hover {
  cursor: pointer;
}

body{background:#DB6A6B}.App{min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.Pomodoro-container{position:relative;display:flex;flex-direction:column;align-items:center;justify-content:space-around;height:100vh;width:100%;box-shadow:0 19px 38px rgba(0,0,0,0.3),0 15px 12px rgba(0,0,0,0.22)}#open-settings,#close-settings{display:block;position:absolute;width:22px;top:10px;right:10px;padding:0;background:transparent;border:none;font-size:24px}#open-settings:hover,#close-settings:hover{cursor:pointer}#open-settings:focus,#close-settings:focus{outline:none}@media only screen and (min-width: 30em) and (max-width: 64.0625em) and (orientation: portrait){#open-settings,#close-settings{width:40px;font-size:40px}}.settings-icon{fill:#fff}.slide-settings{transform:translateX(100%);box-shadow:none !important}.bm-overlay{position:fixed;z-index:1;width:100%;height:100vh;background:rgba(0,0,0,0.3);opacity:1;transition:all 0.2s ease 0s}

.circle{background-color:transparent;width:200px;height:200px;border-radius:1000px;margin:0.5em}.circle-content{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100%;text-align:center}.text{font-size:.875em}.time{font-size:36px}

.svg-circle{position:absolute}circle{transition:stroke-dashoffset 1s linear;transform:rotate(-90deg);transform-origin:50% 50%}

.circle-cycle-container{display:flex;flex-direction:row;justify-content:center;margin-bottom:1em}.circle-cycle{margin:.5em;width:16px;height:16px;border-radius:1000px;background-color:#fff;box-shadow:2px 4px 4px rgba(0,0,0,0.03)}@media only screen and (min-width: 30em) and (max-width: 64.0625em) and (orientation: portrait){.circle-cycle{width:32px;height:32px}}.session-circle-active{background-color:#B6371D}.break-circle-active{background-color:#59C8C6}.cycle-slogan{font-size:20px;text-align:center}

#settings{position:fixed;right:0px;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;z-index:1;width:80%;height:100vh;background-color:#fff;color:#000;transition:all 0.4s ease 0s;box-shadow:#000 -8px 0px 25px -10px}@media (min-width: 64.0625em){#settings{width:30%}}.settings-children{padding-top:16px;width:100%;text-align:center}.settings-manip{display:flex;justify-content:center;align-items:center}.settings-manip button{padding:.75em;font-size:32px;border:none}.settings-manip button:focus{outline:none}.reset-btn{font-size:calc(10px + 2vmin);color:#999999;border:none}

.session{color:#F97F5C}.session button{color:#F97F5C}

.break{color:#59C8C6}.break button{color:#59C8C6}

.cycle{color:#999999}.cycle button{color:#999999}

.circle-play{margin:.5em;padding:.5em;border:2px solid #fff;background-color:transparent;width:40px;height:40px;border-radius:1000px}.circle-play:hover{cursor:pointer}.circle-play-content{display:flex;align-items:center;justify-content:center;height:100%;text-align:center;font-size:20px}.play-icon{width:24px;height:auto;fill:#fff}

